import {PipeTransform, Pipe, Injectable} from '@angular/core';

@Injectable()
@Pipe({name: 'without'})
export class WithoutPipe implements PipeTransform {

  transform(arr:any[], args:any[] = []):any[] {
    return arr.filter(elm => !~args.indexOf(elm));
  }
}



